@use 'base/variables' as v;
@use 'base/mixins' as m;

.busqueda{
    background-color: v.$grisClaro;
    padding: 5rem 0;

    &__heading{
        @include m.heading;
    }
    
}

.formulario{
    margin-top: 2rem;
    display: flex;
    flex-direction: column;
    @include m.tablet {
        @include m.grid(4,3rem);
    }
  
    &__campo{
        padding: 1rem;
        border: 1px solid darken(v.$grisClaro, 20%);
        margin-bottom: 2rem;
        @include m.tablet {
            margin-bottom: 0;
        }
    }

    &__submit{
        @include m.boton(v.$primario, v.$blanco);
        margin-top: 0;

    }
}